.notice,
#ptc-asana-dashboard-error {
  max-width: 700px;
  margin: 7px auto;
  box-sizing: border-box;
}

section#ptc-asana-connect {
  padding: 80px;
}

section#ptc-asana-connect img[title="Asana"] {
  width: 33%;
  margin-bottom: 20px;
  border-bottom: 1px dashed #bccbdb;
  padding-bottom: 40px;
}

section#ptc-asana-connect h2 {
  font-weight: 800;
  letter-spacing: 0.03em;
  color: #243B53;
  font-size: 2em;
  margin: 0.5em 0;
}

section#ptc-asana-connect form {
  margin: 30px 0 30px;
}

section#ptc-asana-connect form div.field-group {
  margin: 2em 0;
}

section#ptc-asana-connect form div.icon-input-field {
  width: 66%;
  text-align: right;
}

section#ptc-asana-connect form div.icon-input-field input {
  width: calc( 100% - 2.1em);
}

section#ptc-asana-connect form p#connection-agreement-text {
  margin: 0.5em 0;
}

section#ptc-asana-connect form label[for="connection-agreement"] {
  display: inline-block;
}

section#ptc-asana-connect-info {
  padding: 5px 3px;
  background: none;
  border: none;
  box-shadow: none;
}

section#ptc-asana-connect-info p.footnote {
  padding: 0 80px;
  margin-top: 1.5em;
}
